/* custom */
.none {
  display: none;
}

.clearfix:after {
  display: block;
  content: "";
  visibility: hidden;
  clear: both;
  height: 0
}

.clearfix {
  zoom: 1
}

/* reset */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
div,
dl,
dt,
dd,
ul,
ol,
li,
p,
blockquote,
pre,
hr,
figure,
table,
caption,
th,
td,
form,
fieldset,
legend,
input,
button,
textarea,
menu {
  margin: 0;
  padding: 0;
}

header,
footer,
section,
article,
aside,
nav,
hgroup,
address,
figure,
figcaption,
menu,
details {
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption,
th {
  text-align: left;
  font-weight: normal;
}

html,
body,
fieldset,
img,
iframe,
abbr {
  border: 0;
}

i,
cite,
em,
var,
address,
dfn {
  font-style: normal;
}

[hidefocus],
summary {
  outline: 0;
}

li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
small {
  font-size: 100%;
}

sup,
sub {
  font-size: 83%;
}

pre,
code,
kbd,
samp {
  font-family: inherit;
}

q:before,
q:after {
  content: none;
}

textarea {
  overflow: auto;
  resize: none;
}

label,
summary {
  cursor: default;
}

a,
button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
em,
strong,
b {
  font-weight: bold;
}

del,
ins,
u,
s,
a,
a:hover {
  text-decoration: none;
}

body,
textarea,
input,
button,
select,
keygen,
legend {
  font: 12px/1.14 arial, \5b8b\4f53;
  color: #333;
  outline: 0;
}

body {
  background: #fff;
}

a,
a:hover {
  color: #333;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f4f4f4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
  margin: 20px 20px;
}
.search-wrapper {
  height: 50px;
  text-align: center;
}

.search-input {
  padding: 10px 10px;
  border-radius: 5px;
  outline: none;
  margin-right: 10px;
  border: 1px solid #dddddd;
}

.search-btn {
  padding: 10px 10px;
  border-radius: 5px;
  border: 1px solid #dddddd;
  background: transparent;
  color: #808080;
  cursor: pointer;
}

.search-btn:hover {
  color: #111111;
}
.table-list-item {
  margin: 10px 0;
  padding: 10px 0;
  border: 1px solid #dddddd;
  background: white;
}



.table-header>span,
.table-list-item>span {
  display: inline-block;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.table-header>span:nth-child(1),
.table-list-item>span:nth-child(1) {
  width: 5%;
}

.table-list-item>span:nth-child(1) {
  padding-left: 5px;
}


.table-header>span:nth-child(2),
.table-list-item>span:nth-child(2) {
  width: 10%;
}


.table-header>span:nth-child(3),
.table-list-item>span:nth-child(3) {
  width: 45%;
}


.table-header>span:nth-child(4),
.table-list-item>span:nth-child(4) {
  width: 10%;
}


.table-header>span:nth-child(5),
.table-list-item>span:nth-child(5) {
  width: 8%;
}


.table-header>span:nth-child(6),
.table-list-item>span:nth-child(6) {
  width: 8%;
}

.title-link {
  display: block;
  padding-right: 20px;
  word-break: break-all;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-decoration: underline;
  cursor: pointer;
}

.title-link:hover {
  text-decoration: underline;
  color: #111111;
}



.loading {
  padding: 20px;
  text-align: center;
}

.load-more-button {
  width: 100%;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #dddddd;
  background: transparent;
  color: #808080;
  cursor: pointer;
}

.load-more-button:hover {
  color: #111111;
}
